@import url("https://fonts.googleapis.com/css2?family=Inter:wght@100;200;300;400;500;600;700&display=swap");

:root {
  --text-primary: #171717;
  --text-primary-hover: #545454;
  --text-secondary: #666666;
  --text-negative: white;
  --line-color: rgb(221, 221, 221);
  --background: white;
  --background-negative: #18181a;
  --switch-btn: url(/public/assets/BrightIcon.svg);
  --switch-btn-bg: ;
  --switch-btn-hover: url(/public/assets/DarkIconNegative.svg);
  --navbar-bg: rgba(221, 221, 221, 0.71);
  --navbar-bg-negative: rgba(34, 34, 34, 0.71);
}

[data-theme="dark"] {
  --text-primary: white;
  --text-primary-hover: #bbbbbb;
  --text-secondary: #171717;
  --text-negative: #171717;
  --line-color: rgb(72, 72, 72);
  background: #18181a;
  --background-negative: white;
  --switch-btn: url(/public/assets/DarkIconNegative.svg);
  --switch-btn-hover: url(/public/assets/BrightIcon.svg);
  --navbar-bg: rgba(34, 34, 34, 0.71);
  --navbar-bg-negative: rgba(221, 221, 221, 0.71);
}

* {
  border: 0;
  margin: 0;
  padding: 0;
  font-family: "Inter", sans-serif;
  scroll-behavior: smooth;
}

body {
  background: var(--background);
}

main,
footer {
  max-width: 720px;
  width: 80%;
  margin: 0 auto;
  padding: 16px;
  color: var(--text-primary);
}

.head img {
  margin: 100px 0px 10px 0px;
  width: 148px;
  height: 148px;
  border-radius: 100%;
}

.head ul {
  list-style-type: none;
  padding-left: 0;
  margin: 5px 0px 20px 0px;
}

.head ul li,
.head ul li a {
  color: var(--text-primary);
  display: inline;
  margin-right: 15px;
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 20px;
}

.head p {
  text-align: justify;
}

.tags {
  margin-top: 25px;
  padding-bottom: 40px;
  border-bottom: 1px solid var(--line-color);
}

.tag {
  display: inline-block;
  margin: 16px 8px 4px 0;
  padding: 8px 12px;
  border-radius: 12px;
  background-color: #d9d9d9;
  color: var(--text-secondary);
}

.fa-code {
  color: #7593dc;
}

.fa-desktop {
  color: #d8575c;
}

.fa-brush {
  color: #64aaa0;
}

.fa-react {
  color: #00d8ff;
}

.fa-briefcase {
  color: #eeca74;
}

.tag i,
.tag p {
  display: inline-block;
}

.tech-stack {
  max-width: 760px;
  margin: 40px 0px 35px 0px;
  color: var(--text-primary);
}

.skills-list {
  display: grid;
  grid-template-columns: auto auto auto;
}

.skills {
  padding-top: 40px;
}

.skill p,
.skill img {
  padding-top: 15px;
  display: inline-block;
  font-size: 16px;
}

.skill p:last-child {
  font-size: 15px;
  color: #878787;
}

footer {
  border-top: 1px solid rgb(221, 221, 221);
  padding-top: 40px;
}

#data-container {
  display: flex;
}

.icon {
  font-size: 2.6vw;
  transition: all 0.15s ease-in-out;
  margin: 11.5vw 4vw 0px 0px;
  color: var(--text-primary);
}

.icon:hover {
  color: var(--text-primary-hover);
}

.button-container a {
  text-decoration: none;
}

::placeholder {
  color: var(--text-primary-hover);
  opacity: 1;
}

.smallform {
  color: var(--text-primary);
  margin-bottom: 4%;
  padding: 4% 49% 4% 4%;
  background: rgba(255, 255, 255, 0.4);
  border-radius: 5px;
  border-style: none;
  box-shadow: 0 4px 30px rgba(0, 0, 0, 0.1);
  backdrop-filter: blur(5.2px);
  -webkit-backdrop-filter: blur(5.2px);
}

.massage {
  color: var(--text-primary);
  margin-bottom: 10%;
  padding: 4% 49% 100% 4%;
  background: rgba(255, 255, 255, 0.4);
  border-radius: 5px;
  border-style: none;
  box-shadow: 0 4px 30px rgba(0, 0, 0, 0.1);
  backdrop-filter: blur(5.2px);
  -webkit-backdrop-filter: blur(5.2px);
}

.submit {
  color: var(--text-primary);
  cursor: pointer;
  transition: all 0.15s ease-in-out;
  padding: 6% 7%;
  background: rgba(255, 255, 255, 0.4);
  border-radius: 5px;
  border-style: none;
  box-shadow: 0 4px 30px rgba(0, 0, 0, 0.1);
  backdrop-filter: blur(5.2px);
  -webkit-backdrop-filter: blur(5.2px);
}

.submit:hover {
  background-color: #989898;
  color: var(--text-negative);
}

.foot {
  font-weight: 400;
  font-size: 14px;
  margin-top: 40px;
  padding-top: 4px;
  border-top: 1px solid rgb(221, 221, 221);
  color: #666666;
  margin-bottom: 11px;
}

html::-webkit-scrollbar {
  background-color: #f3f3f3;
  width: 10px;
}

html::-webkit-scrollbar-track {
  background-color: #f3f3f3;
}
html::-webkit-scrollbar-track:hover {
  background-color: #f3f3f3;
}

html::-webkit-scrollbar-thumb {
  background-color: #babac0;
  border-radius: 16px;
  border: 2px solid #f3f3f3;
}
html::-webkit-scrollbar-thumb:hover {
  background-color: #a0a0a5;
  border: 2px solid #f4f4f4;
}

html::-webkit-scrollbar-button {
  display: none;
}

/* responsive things */
@media only screen and (max-width: 378px) {
  .head ul li {
    font-size: 14px !important;
  }
}

@media only screen and (max-width: 768px) {
  .tag {
    display: block;
    text-align: center;
  }
  .head ul li {
    font-size: 15px;
  }

  #data-container {
    display: block !important;
    text-align: center;
  }

  .smallform {
    max-width: 95%;
    margin-bottom: 2% !important;
    padding: 2% 30% 2% 2% !important;
  }

  .massage {
    max-width: 95%;
    margin-bottom: 3% !important;
    padding: 2% 30% 20% 2% !important;
  }

  .submit {
    cursor: pointer;
    transition: all 0.15s ease-in-out;
    padding: 1.5% 3% !important;
  }

  .icon {
    margin: 3vw !important;
    font-size: 2.3em !important;
  }
}

/* responsive navbar */
#navbar {
  list-style-type: none;
  overflow: hidden;
  width: 100%;
  padding-left: 0;
  position: fixed;
  top: 0;
  font-weight: 600;
  background: var(--navbar-bg);
  box-shadow: 0 1px 10px rgba(0, 0, 0, 0.1);
  backdrop-filter: blur(4.6px);
  -webkit-backdrop-filter: blur(4.6px);
  z-index: 9999999;
}

#navbar li a {
  display: block;
  padding: 10px 16px;
  text-decoration: none;
  color: var(--text-primary);
}

.name {
  font-size: larger;
}

#navbar .name {
  float: left;
  margin: 8.665px 14px 8.665px 14px;
}

#navbar li:not(.name) {
  float: right;
  margin: 10px 4px;
  transition: all 0.15s ease-in-out;
}

#navbar li a:not(.name a, .active a) {
  font-weight: 300;
}

#navbar li:hover:not(.name, .mode) {
  background-color: var(--text-negative);
  border-radius: 5px;
}

#navbar .mode {
  padding: 20px;
  margin: 4px 16px;
  cursor: pointer;
}

.mode {
  background-image: var(--switch-btn);
  background-position: center;
  background-repeat: no-repeat;
  border-radius: 5px;
}

.rspnsv-btn {
  display: none;
  border: none;
  background: var(--line-color);
  /* rgba(255, 255, 255, 0.71); */
  border-radius: 2px;
  color: rgb(160, 160, 160);
  padding: 1px 3px;
}

@media only screen and (min-width: 608px) {
  .mode:hover {
    background-image: var(--switch-btn-hover);
    background-color: var(--background-negative);
    border-radius: 5px;
  }
}

/* small tablet */
@media only screen and (max-width: 608px) {
  .skills-list {
    grid-template-columns: auto auto;
  }

  .head ul {
    display: grid;
    grid-template-columns: auto auto;
  }

  #navbar {
    display: flex;
    flex-direction: column;
    height: auto;
    justify-content: center;
    align-items: center;
  }

  .name {
    margin-bottom: 0px !important;
  }

  .rspnsv-btn {
    display: block;
  }
}
